/* General styles */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Sign-in page container */
.signin-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #f8f9fa;
  padding: 20px;
  box-sizing: border-box;
}

/* Sign-in section styling */
.signin-section {
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
  margin: 20px;
  width: 100%;
  max-width: 400px;
  text-align: left;
  font-size: 1rem; /* Reduced font size */
}

/* Form styling */
.signin-section form {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.signin-section label {
  margin: 10px 0;
  font-size: 1rem; /* Reduced font size */
}

.signin-section input[type="text"],
.signin-section input[type="email"],
.signin-section input[type="password"],
.signin-section input[type="date"] {
  width: 100%;
  padding: 10px;
  margin: 5px 0;
  border-radius: 5px;
  border: 1px solid #ddd;
  font-size: 1rem; /* Reduced font size */
}

.signin-section button {
  background-color: #4CAF50;
  color: white;
  padding: 10px; /* Reduced padding */
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
  font-size: 1rem; /* Reduced font size */
}

.signin-section button:hover {
  background-color: #388E3C;
}

/* Forgot password link styling */
.forgot-password-link {
  display: block;
  text-align: right;
  margin-top: 10px;
  font-size: 0.9rem;
  color: #007BFF;
  text-decoration: none;
}

.forgot-password-link:hover {
  text-decoration: underline;
}

/* Additional form button styling */
form button {
  display: block;
  width: 100%;
  max-width: 200px;
  margin: 20px auto;
  background-color: #6A1B9A;
  color: white;
  padding: 10px; /* Reduced padding */
  border: none;
  border-radius: 5px;
  font-size: 1rem; /* Reduced font size */
  cursor: pointer;
}

form button:hover {
  background-color: #4A148C;
}

/* Label and input styling */
form label {
  margin: 10px 0;
  font-size: 1rem; /* Reduced font size */
}

form input[type="text"],
form input[type="email"],
form input[type="password"],
form input[type="date"] {
  width: 100%;
  padding: 10px;
  margin: 5px 0;
  border-radius: 5px;
  border: 1px solid #ddd;
  font-size: 1rem;
}

form input[type="checkbox"] {
  margin-right: 10px;
}

/* Media queries for responsiveness */
@media (max-width: 768px) {
  .signin-section {
    padding: 15px;
    margin: 10px;
  }

  .signin-section label {
    font-size: 0.9rem; /* Reduced font size */
  }

  .signin-section input {
    font-size: 0.9rem; /* Reduced font size */
  }

  .signin-section button {
    font-size: 0.9rem; /* Reduced font size */
    padding: 8px; /* Reduced padding */
  }

  form button {
    font-size: 0.9rem; /* Reduced font size */
    padding: 8px; /* Reduced padding */
  }

  .forgot-password-link {
    font-size: 0.8rem;
  }
}

@media (max-width: 480px) {
  .signin-section {
    padding: 10px;
    margin: 5px;
  }

  .signin-section label {
    font-size: 0.8rem; /* Reduced font size */
  }

  .signin-section input {
    font-size: 0.8rem; /* Reduced font size */
  }

  .signin-section button {
    font-size: 0.8rem; /* Reduced font size */
    padding: 6px; /* Reduced padding */
  }

  form button {
    font-size: 0.8rem; /* Reduced font size */
    padding: 6px; /* Reduced padding */
  }

  .forgot-password-link {
    font-size: 0.7rem;
  }
}
